import React, { Component } from 'react';
import { inject, observer } from 'mobx-react';
import { Divider, Modal, message, } from 'antd';
import TableLayout from 'Components/TableLayout'
import { toJS } from 'mobx';


@inject('SystemStore')
@observer
class DepartmentTable extends Component {
    render() {
        const { tableData, PageInfo, isLoading } = toJS(this.props.SystemStore)
        //定义表头
        const columns = [
            {
                title: '角色名',
                dataIndex: 'roleName',
                key: 'roleName',
            },
            {
                title: '备注',
                dataIndex: 'remark',
                key: 'remark',
                render: (text) => (
                    <div>{text ? text : '-'}</div>
                )
            },
            {
                title: '操作',
                key: 'x',
                align: 'right',
                render: (text, record) => (
                    <span>
                        <a onClick={e => this.editRole(record)}>编辑</a>
                        <Divider type="vertical" />
                        <a onClick={e => this.delete(record)}>删除</a>
                    </span>
                )

            }
        ];
        // 选中表格选项
        const rowSelection = {
            onSelect: (record, selected, selectedRows) => {
                console.log(record, selected, selectedRows);
                this.props.SystemStore.selectedRows = selectedRows
            },
            onSelectAll: (selected, selectedRows, changeRows) => {
                this.props.SystemStore.selectedRows = selectedRows
            }
        };
        return <div style={{ marginTop: 20 }}>
            <TableLayout rowKey="id" loading={isLoading} onChange={this.pageChange} rowSelection={rowSelection} columns={columns} dataSource={tableData} pagination={PageInfo} />
        </div>
    }
    pageChange = (e) => {
        this.props.SystemStore.PageInfo = e
        this.props.SystemStore.PageInfo.pageIndex = e.current
        const { getData, PageInfo, roleName } = this.props.SystemStore
        getData(2, { pageIndex: PageInfo.pageIndex, pageSize: PageInfo.pageSize, roleName })

    }
    editRole = (record) => {
        this.props.SystemStore.formData = record
        this.props.SystemStore.modalVisible = !this.props.SystemStore.modalVisible
        this.props.SystemStore.getMenuIdsByRole({ id: record.id })

    }
    delete = (record, ) => {
        const { deleteRole, getData, PageInfo, roleName } = this.props.SystemStore
        Modal.confirm({
            title: '提示',
            content: '是否确认删除该条数据？',
            okText: '确认',
            cancelText: '取消',
            onOk: () => {
                deleteRole(1, { id: record.id }).then(res => {
                    message.success('删除成功')
                    getData(2, { pageIndex: PageInfo.pageIndex, pageSize: PageInfo.pageSize, roleName })
                })
            },
        });
    }
    componentDidMount() {
        const { getData, PageInfo, roleName } = this.props.SystemStore
        getData(2, { pageIndex: PageInfo.pageIndex, pageSize: PageInfo.pageSize, roleName })
    }
}
export default DepartmentTable;