import React, { Component } from 'react';
import { inject, observer } from 'mobx-react';
import { toJS } from 'mobx';
import { message, Form, Input, Radio, Tree } from 'antd';
import GlobalModal from 'Components/GlobalModal';
const { TreeNode } = Tree;
@inject('SystemStore')
@observer
class RoleDialog extends Component {

    renderTreeNodes = data =>
        data.map(item => {
            if (item.children) {
                return (
                    <TreeNode title={item.text} key={item.id} dataRef={item}>
                        {this.renderTreeNodes(item.children)}
                    </TreeNode>
                );
            }
            return <TreeNode {...item} />;
        });


    render() {
        const { modalVisible, formData, treeData } = this.props.SystemStore
        const formLabelCol = { span: 4 }
        const formWrapperCol = { span: 18 }
        const { getFieldDecorator } = this.props.form;
        console.log(formData.menuIds);

        return <div>
            {
                modalVisible && <GlobalModal
                    title={JSON.stringify(formData) === '{}' ? '新增' : '编辑'}
                    visible={modalVisible}
                    width={'50%'}
                    onOk={this.handleSubmit}
                    onCancel={e => this.props.SystemStore.modalChange(0)}
                    children={
                        <div>
                            <Form onSubmit={this.handleSubmit} layout='horizontal' labelCol={formLabelCol} wrapperCol={formWrapperCol}>
                                <Form.Item label="角色名">
                                    {getFieldDecorator('roleName', {
                                        rules: [{ required: true, message: '请输入角色名称' }],
                                        initialValue: formData.roleName || ''
                                    })(
                                        <Input placeholder="请输入角色名称" />
                                    )}
                                </Form.Item>
                                <Form.Item label="备注">
                                    {getFieldDecorator('remark', {
                                        initialValue: formData.remark || ''
                                    })(
                                        <Input placeholder="请输入备注" />
                                    )}
                                </Form.Item>
                                <Form.Item label="菜单权限">
                                    <div style={{ height: 450, overflowY: 'auto' }}>
                                        <Tree
                                            checkable
                                            onExpand={this.onExpand}
                                            defaultExpandedKeys={['-1']}
                                            onCheck={this.onCheck}
                                            checkedKeys={formData.menuIds}
                                        >
                                            {this.renderTreeNodes(treeData)}
                                        </Tree>
                                    </div>
                                </Form.Item>
                            </Form>
                        </div>
                    }
                />
            }
        </div>;
    }

    onCheck = checkedKeys => {
        console.log('onCheck', checkedKeys);
        this.props.SystemStore.formData.menuIds = checkedKeys
    };
    //提交表单
    handleSubmit = e => {
        e.preventDefault();
        const { getData, modalChange, addNewRole, formData, PageInfo, roleName } = this.props.SystemStore
        this.props.form.validateFields((err, values) => {
            console.log(values, formData)

            if (!err) {
                if (formData.id) {
                    addNewRole(0, { ...formData, ...values }).then(res => {
                        message.success('更新成功')
                        modalChange(0)
                        getData(2, { pageIndex: PageInfo.pageIndex, pageSize: PageInfo.pageSize, roleName })
                    })
                } else {
                    // formData.menuIds = formData.menuIds.join(',')
                    addNewRole(1, { ...formData, ...values }).then(res => {
                        message.success('添加成功')
                        modalChange(0)
                        getData(2, { pageIndex: PageInfo.pageIndex, pageSize: PageInfo.pageSize, roleName })
                    })
                }
            }
        });
    };
}
const RoleDialogForm = Form.create({ name: 'define' })(RoleDialog);
export default RoleDialogForm;